  --btn-color:#D92948;
  --pink: #E149BF;
}
body {
  margin: 0;
  padding: 0;
  background-color: var(--boss-color);
  overflow-x: hidden;
  overflow-y: auto;
  font-family: Roboto;
  font-size: 17px;
  color:#ffffff;
}
a {
  text-decoration: none;
  cursor: pointer;
}
button {
  cursor: pointer;
  outline: none;
  border:none;
}
img {
  min-width: 100%;
  max-width: 100%;
  height: auto;
}
ul {
  list-style: none;
}

h2 {
