  100% {top: 79%;
    opacity: 1;}
}
.swiper-container {
  overflow: hidden;
}
.slider-hero .swiper-slide  {
  background-color: var(--boss-color);

}
.hero-pagination {
  position: absolute;
  text-align: center;
  top: 111%;
}
.swiper-pagination-bullet {
  width: 26px !important;
  height: 26px !important;
  padding: 4px;
  border: 2px solid #564595;
  top: 50%;
  background-color: transparent !important;
}
.swiper-pagination-bullet-active {
  background-color: var(--pink) !important;
  box-shadow: inset 0px 0px 0px 4px #000000;
}
.fon-box-btn-hero {
  position: absolute;
  top: 5%;
  margin-left: auto;
  margin-right: auto;
  left: 0;
  right: 0;
  width: 400px;
  z-index: 20;
  background-color: #564595;
  border-radius: 20px;
}
