  max-width: 100%;
  background-color: #372A68;
  padding: 15px 67px 32px 67px;
  clip-path: polygon(0 1%, 100% 0, 100% 73%, 64% 100%, 35% 100%, 0 73%);
  border-radius: 20px;
}
.btn-hero {
  position: absolute;
  margin-left: auto;
  margin-right: auto;
  left: 0;
  right: 0;
  top: 60%;
  padding: 20px 30px;
  z-index: 21;
  font-size: 35px;
  line-height: 25px;
  text-align: center;
}
.arr-btn {
  position: absolute;
  margin-left: auto;
  margin-right: auto;
  left: 0;
  right: 0;
  top: 90%;
  width: 165px;
  height: 55px;
  background-color: #564595 ;
  z-index: 21;
  clip-path: polygon(0 0, 37% 15%, 37% 0, 67% 0, 67% 15%, 100% 0, 100% 0, 72% 29%, 53% 49%, 53% 48%, 31% 29%, 0 0);
  animation: arrow-ping 2s infinite linear;
}
@keyframes arrow-ping {
  0% {
    transform: translateY(0);
  }
  50% {
    transform: translateY(10px);
  }
  100% {
    transform: translateY(0);
  }
}
