.skin-card{
  padding: 0 18px;
  display: block;
  &__target{
    color: $pink;
    text-align: center;
    font-size: 15px;
    line-height: 25px;
  }
  &__img{
    position: relative;
    margin-top: 7px;
    &:before{
      content: '';
      position: absolute;
      width: 101.14px;
      height: 117.3px;
      background-image: url(../img/skin-bg.svg);
      background-repeat: no-repeat;
      background-size: 100% auto;
      background-position: center;
      left: 50%;
      top: 50%;
      transform: translate(-50%,-50%);
    }
    img{
      width: 100%;
      position: relative;
      z-index: 2;
    }
  }
  &__price{
    display: flex;
    align-items: center;
    justify-content: center;
    font-size: 20px;
    line-height: 225%;
    color: $purple;
    margin-top: 5px;
    img{
      width: 20px;
      min-width: unset;
      margin-left: 15px;
    }
  }
  &__from{
    font-size: 15px;
    line-height: 25px;
    margin-top: 6px;
    text-align: center;
  }
  &__time{
    display: flex;
    align-items: center;
    justify-content: center;
    min-height: 30px;
    margin-top: 6px;
    padding: 2px 10px;
    font-size: 13px;
    line-height: 120%;
    color: #8787AB;
    background: #313155;
    border-radius: 10px;
    img{
      margin-right: 4px;
      min-width: unset;
    }
  }
}

.catalog-card{
  padding: 10px 0 16px;
  background: linear-gradient(180deg,#492e5273 0%, #15142a 100%);
  &__hours,
  &__bought{
    font-size: 13px;
    line-height: 25px;
    text-align: center;
    color: #8787AB;
  }
  &__img{
    display: block;
    position: relative;
    width: calc(100% - 30px);
    margin: 7px auto 0;
    padding-bottom: 65%;
    &:before{
      content: '';
      position: absolute;
      width: 128.8px;
      height: 149.38px;
      background-image: url(../img/skin-bg.svg);
      background-repeat: no-repeat;
      background-size: 100% auto;
      background-position: center;
      left: 50%;
      top: 50%;
      transform: translate(-50%,-50%);
    }
    img{
      width: 100%;
      height: 100%;
      object-fit: cover;
      left: 0;
      top: 0;
      position: absolute;
      z-index: 2;
    }
  }
  &__title{
    display: block;
    margin-top: 2px;
    font-weight: 600;
    font-size: 15px;
    line-height: 25px;
    color: #fff;
    text-align: center;
    transition: all .4s;
    &:hover{
      color: $pink;
    }
  }
  &__price{
    display: flex;
    align-items: center;
    justify-content: center;
    font-size: 15px;
    line-height: 25px;
    color: $purple;
    margin-top: 5px;
    img{
      width: 20px;
      min-width: unset;
      margin-left: 5px;
    }
  }
  &__slots{
    display: grid;
    grid-template-columns: repeat(5,1fr);
    gap: 5px;
    margin-top: 15px;
    padding: 0 5px;
  }
  &__slot{
    &-wrap{
      padding-bottom: 100%;
      background: #2B2A4E;
      border-radius: 5px;
      position: relative;
      img{
        position: absolute;
        max-width: 100%;
        max-height: 100%;
        left: 50%;
        top: 50%;
        transform: translate(-50%,-50%);
      }
    }
  }
  &__btn{
    width: 100%;
    max-width: 138px;
    margin: 22px auto 0;
  }
  &__bought{
    margin-top: 10px;
  }
}
